/*******************
 * MAIN THEME
 *******************/
body {
  margin: 0;
  padding: 0; }

.overlay {
  background-color: rgba(0, 0, 0, 0.4); }

h1, h2, .h1 {
  font-weight: 700; }

/*************
* Mark: Loader
**************/
.loader-wrapper {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #007bff;
  background: -webkit-gradient(linear, left top, left bottom, from(#56ccf2), to(#007bff));
  background: linear-gradient(#56ccf2, #007bff);
  z-index: 1035;
  overflow: hidden;
  opacity: 1;
  color: white;
  -webkit-transition: opacity 1s;
  transition: opacity 1s; }
  @media (prefers-reduced-motion: reduce) {
    .loader-wrapper {
      -webkit-transition: none;
      transition: none; } }
  .loader-wrapper .flex-row {
    min-height: 100%; }

/*************
* Mark: Navbar
**************/
nav .navbar-brand {
  font: 700 1.25rem "Montserrat";
  max-width: 14rem; }
  nav .navbar-brand img {
    width: 100%; }

/*************
* Mark: Header
**************/
.header-wrapper {
  min-height: 85vh; }

header {
  min-height: 85vh; }
  header p {
