    background: linear-gradient(to bottom right, #56ccf2, #007bff);
    border-radius: 35px;
    -webkit-transition: "max-width" 0.5s, "border-radius" 0.5s;
    transition: "max-width" 0.5s, "border-radius" 0.5s;
    max-width: 80%; }
    @media (prefers-reduced-motion: reduce) {
      header .cta .cta-wrapper {
        -webkit-transition: none;
        transition: none; } }
    @media (max-width: 991.98px) {
      header .cta .cta-wrapper {
